import pageHeader from '../../../components/pageheader/pageHeader.vue';
import filterView from '../../../components/filterView/filterView.vue';
import shopLists from '../../../components/shopLists/shopLists.vue';
import interfaces from '../../../utils/interfaces.js';

export default {
	data() {
		return {
			swipeImgs: [],
			entries: [],
			shopList: [],
			filterData: {},
			indicatorDots: true,
			autoplay: true,
			interval: 2000,
			duration: 500,
			circular: true,
			page: 1,
			size: 5,
			allLoaded: false,
			postData: null,
			upDate: false
		};
	},
	onLoad() {
		this.getBanner();
		this.shopLists();
		this.getFilter();
	},
	onPullDownRefresh(){
		setTimeout(() => {
			this.getBanner();
			this.shopLists();
			this.getFilter();
			uni.stopPullDownRefresh();
		},1000)
	},
	components: {
		pageHeader,
		filterView,
		shopLists
	},
	// onReachBottom() {
	// 	if (!this.allLoaded) {
	// 		this.page++;
	// 		this.upDate = false;
	// 		this.shopLists();
	// 	}
	// },
	watch: {
	},
	mounted() {
	},
	methods: {
		selectCategory(naem) {
			uni.navigateTo({
				url: '../../shops/shops?shop_id=1'  //没有开发分类页面，暂时使用该页面
			});
		},
		navigateToShop(id) {
			uni.navigateTo({
				url: '../../shops/shops?shop_id=' + id
			});
		},
		scrolltolower() {
			if (!this.allLoaded) {
				this.page++;
				this.upDate = false;
				this.shopLists();
			}
		},
		getBanner() {
			this.request({
				url: interfaces.getBanner,
				success: res => {
					// console.log(res);
					this.swipeImgs = res.swipeImgs;
					this.entries = res.entries;
				}
			});
		},
		getFilter() {
			this.request({
				url: interfaces.getfilter,
				success: res => {
					// console.log(res);
					this.filterData = res;
				}
			});
		},
		shopLists() {
			this.allLoaded = false;

			this.request({
				url: interfaces.getShoplists + '/' + this.page + '/' + this.size,
				// url: interfaces.getShoplists,
				data: this.postData,
				method: 'POST',
				success: res => {
					console.log(res);
					// this.shopList = res;
					if (!this.upDate) {
						if (res.length >= this.size) {
							res.forEach(item => {
								this.shopList.push(item);
							});
						} else {
							this.allLoaded = true;
						}
					} else {
						this.shopList = res;
					}
				}
			});
		},
		update(condation) {
			// console.log(condation);
			this.postData = condation;
			this.page = 1;
			this.upDate = true;
			this.shopLists();
		}
	}
};
